import React, { Component } from 'react';

class Sidebar extends Component {
    render() {
        return (
            <div id="sidebar">
                <div className="panel">
                    <h4>Proveedores</h4>
                    <ul className="small-block-grid-2">
                        <li><a className="th" href="http://www.raymarine.com/" target="_blank"><img src="/resources/img/amigos/raymarine.png" alt="Raymarine"/></a></li>
                        <li><a className="th" href="http://www.garmin.com/es/" target="_blank"><img src="/resources/img/amigos/garmin.png" alt="Garmin"/></a></li>
                        <li><a className="th" href="http://satlink.es/" target="_blank"><img src="/resources/img/amigos/satlink.png" alt="Satlink"/></a></li>
                        <li><a className="th" href="http://www.lowrance.es/" target="_blank"><img src="/resources/img/amigos/lowrance.png" alt="Lowrance"/></a></li>
                        <li><a className="th" href="http://www.azimutmarine.es/" target="_blank"><img src="/resources/img/amigos/azimut.png" alt="Azimut"/></a></li>
                        <li><a className="th" href="http://www.glomex.it/index.asp?lang=eng" target="_blank"><img src="/resources/img/amigos/glomex.png" alt="Glomex"/></a></li>
                    </ul>
                </div>
                <div className="panel">
                    <h4>Colaboradores</h4>
                    <ul className="small-block-grid-1">
                        <li><a className="th" href="http://www.cvpa.es/" target="_blank" ><img src="/resources/img/amigos/cvpa.png" alt="Club Vela Puerto Andratx"/></a></li>
                    </ul>
                </div>

                <div className="fb-like" data-href="http://www.electronicamarroig.com" data-send="false" data-layout="button_count" data-width="100" data-show-faces="false"></div>
            </div>
        );
    }
}

export default Sidebar;



