import React, { Component } from 'react';
import Header from './Header';
import Sidebar from './Sidebar';
import Footer from './Footer';

class Layout extends Component {
    render() {
        return (
            <div className="row">
                <div className="small-12 small-centered columns" id="page">
                    <Header/>
                    <div className="row">
                        <div className="small-9 columns" id="contingut">
                            {this.props.children}
                        </div>
                        <div className="small-3 show-for-medium-up columns">
                            <Sidebar/>
                        </div>
                    </div>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Layout;



