import React, { Component } from 'react';
import Slider from 'react-slick'

import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

class HomeSlider extends Component {
    render() {
        const path = "/resources/img/galeria/2016/";
        const images = [
            "Garmin inst.pack.jpg",
            "Raymarine AIS350.jpg",
            "Raymarine a75 radar.jpg",
            "Raymarine musica MS-RA205.jpg",
            "Simrad-Piloto.jpg",
            "Garmin plot.sonda.jpg",
            "Raymarine EV100 hidraulico.jpg",
            "Raymarine antena 37STV.jpg",
            "Raymarine piloto EV100 rueda.jpg",
            "Simrad-Ploter-sonda.2.jpg",
            "Lowrance-Ploter-sonda.jpg",
            "Raymarine IsatPhone.2.jpg",
            "Raymarine c97.jpg",
            "Raymarine radar Quantum.jpg",
            "Simrad-Ploter-sonda.3.jpg",
            "Lowrance-Ploter.2.jpg",
            "Raymarine MT403FG.jpg",
            "Raymarine es97.jpg",
            "Raymarine sonda CP200.jpg",
            "Simrad-Ploter-sonda.jpg",
            "Lowrance-Ploter.jpg",
            "Raymarine Ray260.jpg",
            "Raymarine gps.130.jpg",
            "Raymarine transducers.jpg",
            "Simrad-sonda.jpg",
            "Lowrance-Radar.jpg",
            "Raymarine Ray60.jpg",
            "Raymarine gs125.jpg",
            "Raymarine transponder S.701.jpg",
            "Lowrance-VHF.png",
            "Raymarine Spot gen.3.jpg",
            "Raymarine i50.jpg",
            "Simrad-3G-Radar.jpg",
            "Raimarine i60.jpg",
            "Raymarine a128.jpg",
            "Raymarine i70.jpg",
            "Simrad-Instrumento.2.jpg",
        ];

        const settings = {
            customPaging: function(i) {
                return <a><img src={path + images[i]}/></a>
            },
            autoplay: true,
            dots: true,
            dotsClass: 'slick-dots slick-thumb',
            infinite: true,
            speed: 1000,
            slidesToShow: 1,
            slidesToScroll: 1,
            adaptiveHeight: true,
        };
        return (
            <Slider {...settings}>
                {
                    images.map((image, i ) => (
                        <div key={i}><img src={path +  image} /></div>
                    ))
                }
            </Slider>
        );
    }
}

export default HomeSlider;


