import React, { Component } from 'react';

class Info extends Component {
    render() {
        return (
            <div>
                <h1>Informacin</h1>

                <div className="row">
                    <div className="small-8 columns text-justify">
                        <p>
                            <strong>Electrnica Marroig</strong> es una empresa fundada en 1995, cuenta con una amplia experiencia en el sector de la nutica deportiva.
                        </p>

                        <p>Nuestra actividad principal est centrada en la venta, instalacin y mantenimiento de equipos electrnicos para la navegacin, gps plottes, radares, pilotos y comunicaciones nuticas. Desarrollamos nuestra actividad en todo el litoral mallorqun.</p>
                    </div>

                    <div className="small-4 columns">
                        <img className="bordered-img" src="resources/img/furgo.png" />
                    </div>
                </div>

                <div className="row">
                    <div className="small-12 columns">
                        <h3 className="subheader">Electrnica</h3>
                        <p>Instalacin de sistemas de navegacin, gps plotter, radar, pilotos automticos, sondas, instrumentacin, AIS ...</p>

                        <h3 className="subheader">Comunicaciones</h3>
                        <p>Instalacin de sistemas de comunicacin, radiotelfonos, VHF, BLU ...</p>

                        <h3 className="subheader">Seguridad y salvamento</h3>
                        <p>Radiobalizas, Navtex, transponders ...</p>

                        <h3 className="subheader">Servicio barco pesca profesional</h3>
                        <p>Cajas azules, AIS, telfonos SAT ...</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Info;



