import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

class Products extends Component {
    render() {
        const catalogs = {
            "cat_raymarine": "raymarine",
            "GARMIN_Marine_Oct11": "garmin",
            "cat_satlink": "satlink",
            "cat_lowrance": "lowrance",
            "Glomex_catalog_2012": "glomex",
            "cat_simrad": "simrad"
        };

        const priceList = {
            2016: {
                "raymarine": "raymarine",
                "garmin": "garmin",
                "furuno": "furuno",
                "disvent": "disvent",
                "azimut": "azimut",
                "satlink": "satlink",
                "victron": "victron",
                "sportnav": "sportnav",
            },
            2015 : {
                "ahc": "ahc",
            },
            2013 : {
                "lowrance": "lowrance",
                "bg": "bandg",
                "simrad": "simrad",
                "hrm": "hrm",
                "glomex": "glomex",
            }
        };

        let priceListElements = [];
        let i = 0;
        for (let year in priceList) {
            let yearPriceList = priceList[year];
            for (let pl in yearPriceList) {
                let supplier = yearPriceList[pl];
                priceListElements.push(
                    <li key={i}>
                        <a className="th"
                           href={"/resources/catalogos/" + year + "/" + pl + ".pdf"}
                           target="_blank">
                            <img src={"/resources/img/amigos/" + supplier + ".png"} alt={supplier}/>
                        </a>
                    </li>
                );
                i++;
            }
        }

        return (
            <div>
                <h1>Productos</h1>

                <p>Ponemos a su disposicin una serie de catlogos y listas de precios de los proveedores con los que trabajamos habitualmente:</p>

                <Tabs>
                    <TabList>
                        <Tab>Catlogos</Tab>
                        <Tab>Lista de precios 2016</Tab>
                    </TabList>
                    <TabPanel>
                        <ul className="small-block-grid-3">
                            {
                                Object.keys(catalogs).map((key, i) => (
                                    <li key={i}>
                                        <a className="th" href={"/resources/catalogos/2013/" + key + ".pdf"} target="_blank">
                                            <img src={"/resources/img/amigos/" + catalogs[key] + ".png"} alt={catalogs[key]} />
                                        </a>
                                    </li>
                                ))
                            }
                        </ul>
                    </TabPanel>
                    <TabPanel>
                        <ul className="small-block-grid-4">
                            {priceListElements}
                        </ul>

                        <img className="has-tip" src="/resources/img/info.png" title="Las listas de precios estn sujetas a variacin sin previo aviso. Solicite presupuesto para su seguridad."/>
                    </TabPanel>
                </Tabs>
            </div>
        );
    }
}

export default Products;



