import React, { Component } from 'react';
import { Link } from 'react-router';

class Header extends Component {
    render() {
        return (
            <div>
                <div className="row">
                    <div className="small-12">
                        <div id="header_image">
                            <div id="logo">
                                <Link to="/"><img src="/resources/img/logo.png" alt="Electronica Marroig"/></Link>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className={"small-12 " + location.pathname.replace("/", "")} id="menu">
                        <ul className="inline-list">
                            <li><Link to="/">Inicio</Link></li>
                            <li><Link to="/informacion">Informacin</Link></li>
                            <li><Link to="/productos">Productos</Link></li>
                            <li><Link to="/contacto">Contacto</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;



