import React, { Component } from 'react';
import HomeSlider from './HomeSlider';

class Home extends Component {
    render() {
        return (
            <div>
                <h3>Electrnica Marroig</h3>
                <h4 className="subheader">Venta, instalacin y mantenimiento de equipos electrnicos</h4>

                <div className="row">
                    <div id="galleria" className="small-12 columns">
                        <HomeSlider/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;



